import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import SignIn from '@/components/sign/SignIn'
import SignIndex from '@/components/sign/SignIndex'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/signin'
    },
    {
      path: '/index',
      name: 'SignIndex',
      component: SignIndex
    },
    {
      path: '/signin',
      name: 'SignIn',
      component: SignIn
    }
  ]
})



