<template>
    <div>
        <tempHeader></tempHeader>
        <hr/>
        <div class="freamwork">
            Account:<input type="text" v-model="userInfo.account" placeholder="Input account...">
            <br/>
            Password:<input type="password" v-model="userInfo.passwd" placeholder="Input password...">
            <br/>
            <button v-on:click="signIn">Sign in</button>
            <br/>
        </div>
        <hr/>
        <tempFooter></tempFooter>
    </div>
</template>

<script>
import tempHeader from "@/components/common/TempHeader.vue";
import tempFooter from "@/components/common/TempFooter.vue";

export default {
    name : 'sign',
    components : { tempHeader, tempFooter},
    data () {
        return {
            userInfo : { account : '', passwd : ''},
            responseResult : []
        }
    },
    methods : {
        signIn () {
            this.$axios
            .post('/signin',{
                account : this.userInfo.account,
                passwd : this.userInfo.passwd
            })
            .then(successResponse => {
                this.responseResult = JSON.stringify(successResponse.data)
                if(successResponse.data.code === 200 )
                    this.$router.replace({path : '/index'})
            })
            .catch(failResponse => {
                alert("wrong---")
            })
        },
        signOut () {

        }
    }
}
</script>

<style>

</style>


